import React, { useState } from "react";
import "../App.css";
import "../css/Links.css";
import "animate.css/animate.min.css";
import { makeStyles } from "@material-ui/core/styles";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import MenuItem from "@material-ui/core/MenuItem";
import Menu from "@material-ui/core/Menu";
import MenuIcon from "@material-ui/icons/Menu";
import { Link } from "react-scroll";
import ScrollAnimation from "react-animate-on-scroll";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import UploadForm from "./components/UploadForm";
import ImageGrid from "./components/ImageGrid";
import Modal from "./components/Modal";
import ScrollupButton from './components/ScrollupButton';
import Footer from './components/Footer';

function Logos() {
  const useStyles = makeStyles((theme) => ({
    // Корневой div
    root: {
      flexGrow: 1,
    },
    logo: {
      marginRight: "5px",
    },
    // Кнопка меню
    menuButton: {
      color: "black",
    },
    // Текст лого
    logoTitle: {
      flexGrow: 1,
      color: "black",
    },
    categoriesHeader: {
      margin: 25,
      backgroundColor: "#0f0f0f",
      height: "50vh",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
      borderRadius: "4px",
    },
    categoriesText: {
      color: "white",
      fontFamily: "Roboto",
      paddingLeft: "20px",
      paddingRight: "20px",
      textAlign: "center",
    },
  }));

  const [selectedImg, setSelectedImg] = useState(null);

  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div className={classes.root}>
      <meta
        name="viewport"
        content="width=device-width, initial-scale=1.0, user-scalable=0, minimum-scale=1.0, maximum-scale=1.0"
      />
      <link
        rel="stylesheet"
        href="https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.5.2/animate.min.css"
      />
      <ScrollupButton />
      <AppBar position="static" className="menuBackground">
        <Toolbar>
          <Typography variant="h5" className={classes.logoTitle}>
            <FontAwesomeIcon
              icon={["fab", "dyalog"]}
              className={classes.logo}
            />
            danya
          </Typography>
          <div>
            <IconButton
              edge="start"
              className={classes.menuButton}
              color="inherit"
              aria-label="menu"
              onClick={handleMenu}
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorEl}
              anchorOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              keepMounted
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
              open={open}
              onClose={handleClose}
            >
              <MenuItem onClick={handleClose}>Главная</MenuItem>
              <Link
                onClick={handleClose}
                to="works"
                spy={true}
                smooth={true}
              >
                <MenuItem>Работы</MenuItem>
              </Link>
              <Link
                onClick={handleClose}
                to="contacts"
                spy={true}
                smooth={true}
              >
                <MenuItem>Контакты</MenuItem>
              </Link>
            </Menu>
          </div>
        </Toolbar>
      </AppBar>

      <ScrollAnimation animateIn="fadeInDown">
        <div className={classes.categoriesHeader}>
          <Typography variant="h4" className={classes.categoriesText}>
            Логотипы
          </Typography>
        </div>
      </ScrollAnimation>
      <ScrollAnimation animateIn="fadeInDown">
      <UploadForm />
      </ScrollAnimation>
      <div id="works"></div>
      <ImageGrid setSelectedImg={setSelectedImg} />
      {selectedImg && <Modal selectedImg={selectedImg} setSelectedImg={setSelectedImg}
      exit={{ opacity: 0 }}
      />} 
      <div id="contacts"></div>
      <Footer />
    </div>
  );
}

export default Logos;
