import React from 'react';
import '../App.css';
import '../css/Links.css';
import 'animate.css/animate.min.css';
import ScrollAnimation from 'react-animate-on-scroll';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu';
import LogoCard from '../cards/LogoCard';
import PosterCard from '../cards/PosterCard';
import AdCard from '../cards/AdsCard';
import MagazineCard from '../cards/MagazineCard';
import FirmDesignCard from '../cards/FirmDesignCard';
import PixelArtCard from '../cards/PixelartCard';
import { Grid } from "@material-ui/core";
import {Link} from 'react-scroll';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { library } from '@fortawesome/fontawesome-svg-core';
import { fab, faTelegram } from '@fortawesome/free-brands-svg-icons';
import { fas } from '@fortawesome/free-solid-svg-icons';
import { faEnvelope } from '@fortawesome/free-regular-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import ImageSlider from './slider-components/ImageSlider';
import Footer from './components/Footer';
import { SliderData } from './slider-components/SliderData';
library.add(fab, fas, faTelegram, faEnvelope);

const useStyles = makeStyles((theme) => ({
  // Корневой div
  root: {
    flexGrow: 1,
  },
  logo: {
    marginRight: '5px',
  },
  // Кнопка меню
  menuButton: {
    color: 'black',
  },
  // Текст лого
  logoTitle: {
    flexGrow: 1,
    color: 'black',
  },
  // Приветственный div
  welcomeHeader: {
    margin: 25,
    backgroundColor: '#0f0f0f',
    height: '50vh',
    display: 'flex',
    justifyContent:'center',
    alignItems: 'center',
    borderRadius: '4px',
  },
  // Приветственный текст
  welcomeText: {
    color: 'white',
    fontFamily: 'Roboto',
    paddingLeft: '20px',
    paddingRight: '20px',
    textAlign: 'center',
    },
    // box categories
  categoriesDiv: {
    marginTop: '7vh',
    marginBottom: '7vh',
    backgroundColor: 'transparent',
    display: 'flex',
    justifyContent:'center',
    alignItems: 'center'
  },
  // categories text
  categoriesText: {
    marginTop: '20px',
    color: 'black',
    fontFamily: 'Roboto'
    },
    media: {
      height: 140,
    },
  gridContainer: {
    paddingLeft: "10vw",
    paddingRight: "10vw",
    overflowY: 'hidden',

  },
    // footer background
  footerBack: {
    background: '#0f0f0f',
    height: 'auto',
    width: '100%',
    borderRadius: '4px',
    marginTop: '50px',
  },
  footerBox: {
    margin: '25px',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
    // footer heading text
  contactsHeader: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    color: 'white',
    textAlign: 'left',
    position: 'relative',
    marginTop: '30px',
    marginBottom: '30px',
    fontSize: 'calc(20px + 2vw)',
    fontWeight: '100',

  },
  contactLine:{
    marginLeft: '40px',
    marginRight: '40px',
    color: '#2f2f2f',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },

  moreLine: {
    marginLeft: '45px',
    marginRight: '45px',
    marginTop: '40px',
    color: '#c6c6c6',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },

  footerLogo: {
    marginRight: '10px',
    fontSize: 'calc(15px + 2vw)',   
  },
  footerIcons: {
    marginRight: '10px',
  },
}));

export default function MenuAppBar() {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleMenu = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (
    <div className={classes.root}>
      <meta name="viewport" content="width=device-width, initial-scale=1.0, user-scalable=0, minimum-scale=1.0, maximum-scale=1.0"/>
      <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.5.2/animate.min.css"/>
      
      <AppBar position="static" className="menuBackground">
        <Toolbar>
          <Typography variant="h5" className={classes.logoTitle}>
          <FontAwesomeIcon icon={["fab", "dyalog"]} className={classes.logo}/>
            danya    
          </Typography>
            <div>
              <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu"  onClick={handleMenu}>
            <MenuIcon />
          </IconButton>
              <Menu
                id="menu-appbar"
                anchorEl={anchorEl}
                anchorOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                keepMounted
                transformOrigin={{
                  vertical: 'top',
                  horizontal: 'right',
                }}
                open={open}
                onClose={handleClose}
             
              >
                <MenuItem onClick={handleClose}>Главная</MenuItem>
                <Link onClick={handleClose} to="categories" spy={true} smooth={true}><MenuItem>Категории</MenuItem></Link>
                <Link onClick={handleClose} to="lastWorks" spy={true} smooth={true}><MenuItem>Последние работы</MenuItem></Link>
                <Link onClick={handleClose} to="footer" spy={true} smooth={true}><MenuItem>Контакты</MenuItem></Link>
                
              </Menu>
            </div>
        </Toolbar>
      </AppBar>
      <ScrollAnimation animateIn="fadeInDown">
      <div className={classes.welcomeHeader}>
      <Typography variant="h4" className={classes.welcomeText}>
            Добро пожаловать на сайт дизайнера
          </Typography>
      </div>
      </ScrollAnimation>

<section class="categories" id="categories">
<div className={classes.categoriesDiv}>
<ScrollAnimation delay={200} animateIn="fadeInDown">
      <Typography variant="h5" className={classes.categoriesText}>
           Категории
          </Typography>
  </ScrollAnimation>
      </div>
  <Grid
      container
      spacing={4}
      className={classes.gridContainer}
  >
    <Grid item xs={12} sm={6} md={4}>
    <ScrollAnimation delay={200} animateIn="fadeInDown">
      <LogoCard />
    </ScrollAnimation>
    </Grid>
    <Grid item xs={12} sm={6} md={4}>
    <ScrollAnimation delay={400} animateIn="fadeInDown">
      <PosterCard />
    </ScrollAnimation>
    </Grid>
    <Grid item xs={12} sm={6} md={4}>
    <ScrollAnimation delay={600} animateIn="fadeInDown">
      <AdCard />
    </ScrollAnimation>
    </Grid>
    <Grid item xs={12} sm={6} md={4}>
    <ScrollAnimation delay={200} animateIn="fadeInUp">
      <MagazineCard />
    </ScrollAnimation>
    </Grid>
    <Grid item xs={12} sm={6} md={4}>
    <ScrollAnimation delay={400} animateIn="fadeInUp">
      <FirmDesignCard />
    </ScrollAnimation>
    </Grid>
    <Grid item xs={12} sm={6} md={4}>
    <ScrollAnimation delay={600} animateIn="fadeInUp">
      <PixelArtCard />
    </ScrollAnimation>
    </Grid>
  </Grid>

<hr className={classes.moreLine}></hr>
<ScrollAnimation delay={200} animateIn="fadeIn">
  <a href="/other" class="categories-more">
      Другое 
      <FontAwesomeIcon icon={["fas", "caret-down"]} className={classes.moreIcon}/>
  </a>
  </ScrollAnimation>
</section>

      <section id="lastWorks">
        <div className={classes.categoriesDiv}>
        <ScrollAnimation delay={100} animateIn="fadeInDown">
          <Typography variant="h5" className={classes.categoriesText}>
            Последние работы
          </Typography>
          </ScrollAnimation>
        </div>
        <ScrollAnimation delay={200} animateIn="fadeIn">
        <ImageSlider slides={SliderData} />
        </ScrollAnimation>
      </section>
    <Footer />
    </div>
  );
}